<template>
	<div class="page-content-detail hospital-detail">
		<div class="detail-info">
			<h1 class="name">{{sourceDate.name}}</h1>
			
			<div>
				<img :src="sourceDate.headimg">
			</div>

			<p v-for="item in sourceDate.content">{{item}}</p>
		</div>

		<DetailFooter :data="detailFooter" />
	</div>
</template>

<script>
	import DetailFooter from '@/components/DetailFooter.vue'

	export default {
		name: 'hospital-detail',
		components: { DetailFooter },
		data() {
			return {
				sourceDate: {},
				detailFooter: {
					content: '更多合作医院'
				}
			}
		},
		mounted() {
			if (sessionStorage.hospital) {
				let _hospital = JSON.parse(sessionStorage.hospital);

				_hospital.map(item => {
					if (item.id == this.$route.query.id) {
						this.sourceDate = item;
					}
				})
			}
		},
		methods: {
			
		}
	}
</script>

